//纸箱出入库
import zTable from "@/components/z-table/z-table.vue"
export default {
	name:'boxIn',//纸箱出入库
	components:{zTable},
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			dataTableList:[],
			errorContent:'数据加载中...',
			tableHeight:0,//表格高度
			tableColumns:[
				{
					key: 'bc_No',
					title: '订单号',
					width: 180,
					isResize:true
				},{
					key: 'ct_Desc',
					title: '客户名称',
					width: 200,
					isResize:true
				},{
					key: 'bi_ProdName',
					title: '产品名称',
					width: 200,
					isResize:true
				},{
					key: 'bi_OrderQty',
					title: '数量',
					width: 180,
					isResize:true
				},{
					key: 'Specs',
					title: '规格',
					width: 180,
					isResize:true
				}
			]
		}
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	}
}
